export default {
	data() {
		return {
			events: [{
				name: "英语单词",
				text: "使用扇贝记单词记30个单词",
				classify: "INPUT",
				type: "EVERYDAY",
				status: "TODO",
			},{
				name: "英语口语",
				classify: "INPUT",
				type: "EVERYDAY",
				status: "TODO"
			},{
				name: "公众号文章",
				text: "在我的公众号是输出高质量文章",
				classify: "OUTPUT",
				type: "WEEKLY",
				status: "fulfill",
				timeDo: "8:30"
			}],
			modalName: null,
			listTouchStart: 0,
			listTouchDirection: null,
		};
	},
	computed: {
		
	},
	methods:{
		listItemType(type){
			let result;
			if (type == "EVERYDAY") {
				result = "每日必做"
			}else if (type == "WEEKLY"){
				result = "每周必做"
			}else{
				result = "今日计划"
			}
			return result
		},
		
		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},
		
		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},
		
		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		}
	}
	
}
